:root {
  --light: #faf8f8;
  --dark: #141021;
  --secondary: #284b63;
  --tertiary: #84a59d;
  --visited: #afbfc9;
  --gray: #4e4e4e;
  --lightgray: #f0f0f0;
  --outlinegray: #cacaca;
}

[saved-theme="dark"] {
  --light: #1e1e21 !important;
  --dark: #fbfffe !important;
  --secondary: #6b879a !important;
  --visited: #4a575e !important;
  --tertiary: #84a59d !important;
  --gray: #d4d4d4 !important;
  --lightgray: #292633 !important;
  --outlinegray: #444444 !important;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #faf8f8;
  background-color: var(--light);
}

pre, code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: relative;
  min-height: 100vh;
}

.App-content {
  padding: 0 30vw 4em;
  font-size: 1rem;
}

@media screen and (max-width: 1200px) {
  .App-content {
    padding: 0 5vw 4em;
  }
}

/* Typography */
h1, h2, h3, h4, h5, h6, thead {
  color: var(--dark);
  font-weight: revert;
  margin: 2rem 0 0;
  padding: 2rem auto 1rem;
}

h2, h3 {
  margin: 1.5rem 0 0;
}

p, ul, text, a, tr, td, ol, ul {
  color: var(--gray);
  fill: var(--gray);
  font-weight: revert;
  margin: 0.8rem 0;
  padding: revert;
  line-height: 1.6rem;
}

ol, ul {
  padding-left: 1.5em;
  margin: 0;
}

ol p, ul p {
  margin: 0.2rem 0;
}

table {
  border: 1px solid var(--outlinegray);
  width: 100%;
  padding: 1.5em;
  border-collapse: collapse;
}

td, th {
  padding: 0.2em 1em;
  border: 1px solid var(--outlinegray);
}

img {
  padding: 10px;
  max-width: 100%;
  border-radius: 3px;
  margin: 1em 0;
}

sup {
  line-height: 0
}

blockquote {
  margin-left: 0em;
  border-left: 3px solid var(--secondary);
  padding-left: 1em;
  transition: border-color 0.2s ease;
}

a {
  font-size: 1em;
  color: var(--secondary);
  transition: color 0.3s ease !important;
  cursor: pointer;
}

a:hover {
  color: var(--tertiary);
}

pre {
  padding: 0.75em;
  border-radius: 3px;
}

code {
  font-size: 0.85em;
  padding: 0.15em 0.3em;
  border-radius: 5px;
  background: var(--outlinegray);
}

pre.highlight {
  background: #2e2e2e !important;
}

pre.highlight > code {
  color: #eee;
  padding: 0;
  background: none !important;
}

hr {
  width: 100%;
  margin: 2em 0;
  height: 2px;
  border-radius: 1px;
  border-width: 0;
  color: var(--outlinegray);
  background-color: var(--outlinegray);
}


.settings-item {
  margin: 2em 2em 2em 0;
  min-width: 30px;
}

.settings-item, .settings-item * {
  cursor: pointer;
}

.settings-bar {
  display: flex;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
}

.settings-item svg {
  opacity: 0.4;
  transition: opacity 0.3s ease;
}

.settings-item svg:hover {
  opacity: 1;
}

.helpbutton svg {
  fill: var(--gray);
  width: 24px;
  height: 30px;
}

.darkmode svg {
  position: absolute;
  width: 24px;
  height: 24px;
  top: calc(50% - 13px);
  fill: var(--gray);
}

.ProseMirror * {
  transition: background 0.3s ease;
}

.ProseMirror p.is-editor-empty:first-child::before {
  color: var(--outlinegray);
  content: attr(data-placeholder);
  float: left;
  height: 0;
  pointer-events: none;
}


.ProseMirror:focus {
  outline: none;
}

span[data-time] {
  padding: 0.15em 0.3em;
  border-radius: 5px;
  position: relative;
  white-space: nowrap;
}

span[data-time].highlight {
  background: #ff3333bb !important;  
  transition: background 0.3s ease;
}

span[data-time]::after {
  visibility: hidden;
  opacity: 0;
  content: attr(locale-string);
  position: absolute;
  bottom: 100%;
  left: 0;
  background: var(--light);
  padding: 0.15em 0.3em;
  margin-bottom: 5px;
  border-radius: 5px;
  border: 1px solid var(--outlinegray);
  white-space: nowrap;
  overflow-x: visible;
  transition: opacity 0.3s ease;
}

span[data-time]:hover::after {
  opacity: 1;
  visibility: visible;
}

.has-focus {
  border-radius: 5px;
  background: var(--lightgray);
}

.newline-handle {
  padding: 1em 0;
  opacity: 0.1;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

.newline-handle:hover {
  opacity: 0.8;
}



.help {
  padding: 4em 0;
}

.help > .back {
  cursor: pointer;
}

.item-container {
  display: inline-flex;
  flex-wrap: wrap;
  grid-gap: 2em;
  gap: 2em;
}

.item {
  max-width: 300px;
  display: flex;
  flex-direction: column;
}

.item.wide {
  max-width: calc(600px + 2em);
}

.item-container .preview {
  padding: 1em;
  border-radius: 5px;
  border: 1px solid var(--outlinegray);
  margin-top: auto;
}

.preview * {
  margin: 0;
}

.preview hr {
  margin: 1em 0;
}

.preview img {
  padding: 0;
}

.overlay {
  position: absolute;
  color: var(--dark);
  width: 280px;
  text-align: center;
  font-size: 1rem;
  z-index: 999;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0.9;
  pointer-events: none;
  transition: opacity 0.4s ease;
}




.visualization {
  position: relative;
  min-height: 300px;
}

.spacer {
  min-height: 4em;
}

.visualization:hover > .overlay {
  opacity: 0.1;
} 

.config-item > input[type="checkbox"] {
  visibility: hidden;
  margin-right: 2em;
}

.config-item {
  margin: 1em;
  position: relative;
  display: flex;
}

.config-item h3 {
  margin: 0;
}

.config-item > .desc {
  display: inline-block;
}

.config-item > label {
  padding: 2px;
  border: 1px solid var(--outlinegray);
  cursor: pointer;
  height: 16px;
  width: 16px;
  left: 0;
  top: 0;
  position: absolute;
  border-radius: 5px;
  margin-right: 2em;
}

.config-item > label:after {
  content: "";
  margin: 4px;
  top: 0;
  left: 0;
  position: absolute;
  height: 12px;
  width: 12px;
  opacity: 0;
  border-radius: 3px;
  background-color: var(--secondary);
}

input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

